<template>
  <article class="flex flex-col mb-6 rounded-3xl overflow-hidden filter shadow-2xl">
    <div class="bg-company-color-200 flex flex-col justify-start p-2">
      <a :href="BtnUrl" class="text-3xl font-bold mx-3 text-gray-200 hover:text-gray-500 pb-2">{{
        Header
      }}</a>
    </div>
    <!-- Article Image -->
    <a
      :href="BtnUrl"
      class="h-64 flex hover:opacity-75"
    >
      <img
        :src="require(`@/assets/${AvatarSrcComp}`)"
        class="w-full object-cover object-center"
        :alt="AvatarAltText"
      />
    </a>
    <div class="bg-white flex flex-col justify-start p-6">
      <a :href="BtnUrl" class="pb-6"
        ><slot class="overflow-hidden">Text will come soon</slot></a
      >
      <a :href="BtnUrl" class="uppercase text-gray-800 hover:text-black"
        ><slot v-if="BtnUrl" name="buttonText">Continue reading...</slot>
        <i class="fas fa-arrow-right"></i
      ></a>
    </div>
  </article>
</template>

<script>
export default {
  props: ["AvatarSrc", "AvatarAltText", "BtnUrl", "Header"],
  computed: {
    AvatarSrcComp() {
      if (this.AvatarSrc) {
        return this.AvatarSrc;
      } else {
        return "full_logo.webp";
      }
    },
  },
};
</script>

