import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/pages/Home.vue";
import About from "../views/pages/About.vue";
import Contact from "../views/pages/Contact.vue";
import FusionFactoryGame from "../views/pages/games/FusionFactory.vue"
import FusionFactoryGameRulesDutch from "../views/pages/games/FusionFactoryRulesDutch.vue"
import FusionFactoryRulesEnglish from "../views/pages/games/FusionFactoryRulesEnglish.vue"

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/about",
    name: "About",
    component: About,
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact,
  },
  {
    path: "/games/fusionFactory",
    name: "FusionFactoryGame",
    component: FusionFactoryGame,
  },
  {
    path: "/games/fusionFactory/rulesDutch",
    name: "FusionFactoryRulesDutch",
    component: FusionFactoryGameRulesDutch,
  },
  {
    path: "/games/fusionFactory/rulesEnglish",
    name: "FusionFactoryRulesEnglish",
    component: FusionFactoryRulesEnglish,
  },
  // 404 catchall
  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: Home
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
